import cartItem from './api/cartItem'

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('CartItem', component.exports)
}
export default {
  props: {
    cartItem: {
      type: Object,
      default: null
    }
  },
  methods: {
    update (quantity) {
      cartItem
        .update(this.cartItem.cart_item_token, quantity)
        .then(response => response.data)
        .then(response => {
          this.$emit('update')
        })
    },
    remove () {
      cartItem.delete(this.cartItem.cart_item_token)
        .then(response => response.data)
        .then(response => {
          this.$emit('delete')
        })
    }
  }
}
